import React from "react";
import { NavLink } from "react-router-dom";
import Velocity from "velocity-animate";
import { VelocityComponent, VelocityTransitionGroup } from "velocity-react";
import logo from "../../images/logo/logo_transparent.png";
import Collapse from "react-md/lib/Helpers/Collapse";
import Button from "react-md/lib/Buttons/Button";
import Media from "react-md/lib/Media";
import "./navbarstyles.css";

/*
* NavBar component. Different displays for desktop/tablet vs mobile
*/
class NavBar extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            collapsed: false,
            windowWidth: window.innerWidth
        };
    }

    // Velocity.js scolling function
    scroll(event, offset = 0) {
        offset = offset - 80;
        Velocity(document.getElementById(event), "scroll", {
            duration: 800,
            easing: [0.99, 0, 0.69, 0],
            offset
        });
    }

    // Changes state as window gets resized
    handleResize = () => {
        this.setState({
            windowWidth: window.innerWidth
        });
    };

    // Adds window size event listener on mount
    componentDidMount() {
        window.addEventListener("resize", this.handleResize);
    }

    // Removes window size event listener on unMount
    componentWillUnmount() {
        window.removeEventListener("resize", this.handleResize);
    }

    toggleCollapse = () => {
        this.setState({ collapsed: !this.state.collapsed });
    };

    render() {
        const navRoutes = [
            { route: "come home", offset: 0 },
            { route: "my expertise", offset: 0 },
            { route: "my work", offset: -20 },
            { route: "get in touch", offset: 0 }
        ];

        return (
            <nav className="navbar-wrapper">
                {this.state.windowWidth >= 767
                    ? <DesktopNav scroll={this.scroll} routes={navRoutes} />
                    : <div className="mobile-nav">
                          <Button
                              className="menutoggle-button"
                              raised
                              label="Menu"
                              onClick={this.toggleCollapse}
                              style={{ marginBottom: 16 }}
                          />
                          <VelocityTransitionGroup
                              runOnMount={false}
                              enter={{ animation: "slideDown", easing: 'spring', duration: 500 }}
                              leave={{ animation: "slideUp", duration: 100}}
                          >
                              {this.state.collapsed
                                  ? <MobileNav
                                        collapse={this.toggleCollapse}
                                        scroll={this.scroll}
                                        routes={navRoutes}
                                    />
                                  : null}
                          </VelocityTransitionGroup>
                      </div>}
            </nav>
        );
    }
}

const DesktopNav = props =>
    <ul className="desktopnav-wrapper">
        <div className="logo-wrapper">
            <img src={logo} alt="Cole Gottdank's Logo!" />
        </div>
        {props.routes.map(route =>
            <div className="navlink-wrapper">
                <NavLink
                    key={route.route}
                    className="navlink"
                    activeClassName="navlink-active"
                    onClick={() =>
                        props.scroll(
                            route.route.replace(/\s/g, ""),
                            route.offset
                        )}
                    to={`/${route.route.replace(/\s/g, "")}`}
                >
                    {route.route}
                </NavLink>
            </div>
        )}
    </ul>;

/*
* @return list of NavLinks
*/
const MobileNav = props =>
    <ul className="mobilenav-wrapper">
        {props.routes.map(route =>
            <NavLink
                key={route.route}
                className="navlink"
                activeClassName="navlink-active"
                onClick={() => {
                    props.scroll(route.route.replace(/\s/g, ""), route.offset);
                    props.collapse();
                }}
                to={`/${route.route.replace(/\s/g, "")}`}
            >
                {route.route}
            </NavLink>
        )}
    </ul>;

export default NavBar;



