import React from "react";
import Velocity from "velocity-animate";
import ScrollMagic from "scrollmagic";
import Paper from "react-md/lib/Papers";
import Card from "react-md/lib/Cards/Card";
import CardTitle from "react-md/lib/Cards/CardTitle";
import CardText from "react-md/lib/Cards/CardText";
import Divider from "react-md/lib/Dividers";
import decoupled from "../../images/expertise/decoupled.jpg";
import speed from "../../images/expertise/speed.png";
import ecommerce from "../../images/expertise/ecommerce.jpg";
import development from "../../images/expertise/development.jpg";
import responsive from "../../images/expertise/responsive.png";
import "./section2styles.css";

class Section2 extends React.Component {
	scroll(event, offset = 0) {
		Velocity(document.getElementById(this), "scroll", {
			duration: 1000,
			easing: "easeInBack",
			offset
		});
	}
	render() {
		const settings = {
			className: "centered",
			dots: true,
			arrows: true,
			slidesToShow: 1,
			slidesToScroll: 1,
			initialSlide: 0,
			infinite: false,
			speed: 500,
			focusOnSelect: true,
			useCSS: true
		};

		const skills = [
			{
				title: "Modular Code",
				subtitle: "Do it right the first time",
				desc:
					"Crafting decoupled, modular, & scalable code the first time will drastically lower maitence cost. React.js & CSS-modules. Welcome to the future of web development!",
				img: decoupled
			},
			{
				title: "Speed",
				subtitle: "Code with some oomph",
				desc:
					"To understand how I can improve your websites speed, let's take a look at this difficult math equation:\n(Less rendering) + (virtual DOM) = VROOM",
				img: speed
			},
			{
				title: "Responsive Design",
				subtitle: "Try to resize me",
				desc: "A poor responsive design is a massive inconvenience for tablet/mobile users. Most tablet/mobile users are browsing your shop while laying back and watching Netflix. If even just one bug occurs, purchases may be canceled!",
				img: responsive
			},
			{
				title: "eCommerce",
				subtitle: "Shopify GOD",
				desc: "As a React.js programmer, I'm able to craft",
				img: ecommerce
			},
			{
				title: "App Development",
				subtitle: "Capatalize on the growing market",
				desc:
					"\"U.S. adults spent 59% of their time on mobile and 41% on desktop, but just 15% of their dollars on mobile and a staggering 85% of their dollars on desktop.\"\n\tIT'S FREAKIN TIME TO FIX THIS!\nUtilizing MVP structure, I can craft an extremely user-friendly Android app. Let's capatalize on the mobile market together.",
				img: development
			},
			{
				title: "Software Engineering",
				subtitle: "Wait, you also are a SOFTWARE ENGINEER!?",
				desc: "Heh, yes! I'm a Software Engineering student with 5+ years of Java development experience and 2+ years of C development! The focus of my degree is to craft production scale, optimized, scalable, decoupled, and testable code. Utilizing these skills makes my web code just that much better.",
				img: decoupled
			}
		];
		return (
			<section className="section2-wrapper">
				<div className="content-wrapper" id="myexpertise">
					<Expertise skills={skills} />
				</div>
			</section>
		);
	}
}

const Expertise = props =>
	<section className="md-grid">
		{props.skills.map(skill =>
			<Paper
				className="md-cell md-cell--5-desktop md-cell--4-tablet md-cell--4-phone skill-paper"
				zDepth={0}
				raiseOnHover={true}
			>
				<Card
					className="skill-card"
					style={{ "background-image": `url(${skill.img})` }}
				>
					<section className="card-wrapper">
						<CardTitle
							title={skill.title}
							subtitle={skill.subtitle}
						/>
						<Divider />
						<CardText>
							<p>
								{skill.desc.split("\n").map(desc =>
									<span key={desc}>
										{desc}
										<br />
									</span>
								)}
							</p>
						</CardText>
					</section>
				</Card>
			</Paper>
		)}
	</section>;

export default Section2;



